var $ = jQuery;
var api = {
    post: function (action, data) {
        var postUrl = new URL(window.ajaxUrl);
        postUrl.searchParams.append('action', action);
        return $.post({
            url: postUrl.toString(),
            data: JSON.stringify(data),
            contentType: 'application/json; charset=utf-8',
            dataType: 'json'
        });
    },
    get: function (url) {
        return $.get({ url: url });
    }
};
export default api;
